import React, { useState } from 'react';
import { Link } from "gatsby"
import 'assets/stylesheets/components/__components.scss';
import { Button } from 'reactstrap';

import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavbarText
} from 'reactstrap';


//class Header extends React.component

 const Header = () => {
  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => setIsOpen(!isOpen);
  return (
    <div>
      <Navbar fixed="top" color="dark" dark expand="lg">
        <NavbarBrand><Link to="/">    </Link></NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="container" navbar>
          <NavItem>
            <Link to="/"> COVID19  </Link>
            </NavItem>
            <NavItem>
            <Link to="/geolocate/">  Camps   </Link>
            </NavItem>
            <NavItem>
            <a style={{ color: 'greenyellow' }} href="http://kostasp3.sg-host.com/">  </a>
            </NavItem>
          </Nav>
          <NavbarText><Button color="success" border-radius= "20" href="http://kostasp3.sg-host.com/" target="_blank"></Button>{''}</NavbarText>
        </Collapse>
      </Navbar>
    </div>
  );
 }

 export default Header;
